@use './variables' as *;

/*****************
    Defaults
******************/

/* Reset */
html {
    scroll-behavior: smooth;
    height: 100%;
}

*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
}

body {
    font-family: $font-sans;
    margin: auto;
    position: relative;
    height: 100%;
}

/* Element Defaults */
img,
picture {
    display: block;
    max-width: 100%;
}

ul,
ol {
    padding-inline-start: 0;
}

a {
    text-decoration: none;
}

[hidden] {
    display: none !important;
}

input,
textarea {
    background-color: $white;
    border: 2px solid $grey-200;
    max-width: 100%;
    outline: none;
    padding: .5em;
    transition: border-color 200ms ease;

    &:hover {
        border-color: $primary-clr;
    }

    &:focus {
        border-color: $primary-clr;
    }
}

/* Helpers - Container */
.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}

@media (min-width: 992px) {
    .container {
        width: 970px;
    }
}

@media (min-width: 1200px) {
    .container {
        width: 1170px;
    }
}

/* Helpers - Text */
.text-upper {
    text-transform: uppercase;
    letter-spacing: -0.5px;
}


/* Helpers - CK Editor */
#editor,
.ck-editor {
    max-width: 800px;
}