/*****************
    Variables
******************/
:root {
  --hue: 248;
  --sec-hue: 37;
}

/*****************
    Defaults
******************/
/* Reset */
html {
  scroll-behavior: smooth;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
}

body {
  font-family: "Inter", -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
  margin: auto;
  position: relative;
  height: 100%;
}

/* Element Defaults */
img,
picture {
  display: block;
  max-width: 100%;
}

ul,
ol {
  padding-inline-start: 0;
}

a {
  text-decoration: none;
}

[hidden] {
  display: none !important;
}

input,
textarea {
  background-color: #fff;
  border: 2px solid #e2e2e7;
  max-width: 100%;
  outline: none;
  padding: 0.5em;
  transition: border-color 200ms ease;
}
input:hover,
textarea:hover {
  border-color: hsl(var(--hue), 92%, 25%);
}
input:focus,
textarea:focus {
  border-color: hsl(var(--hue), 92%, 25%);
}

/* Helpers - Container */
.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}
/* Helpers - Text */
.text-upper {
  text-transform: uppercase;
  letter-spacing: -0.5px;
}

/* Helpers - CK Editor */
#editor,
.ck-editor {
  max-width: 800px;
}

/*****************
    Buttons
******************/
.btn, .btn-link, .btn-outline-primary, .btn-primary {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 0.25em;
  border: 1.8px solid transparent;
  border-radius: 4px;
  cursor: pointer;
  font-size: 15px;
  font-weight: 500;
  padding: 0.45em 0.75em;
  text-decoration: none;
  transition: background-color 200ms ease-in;
}
.btn:focus, .btn-link:focus, .btn-outline-primary:focus, .btn-primary:focus, .btn:focus-visible, .btn-link:focus-visible, .btn-outline-primary:focus-visible, .btn-primary:focus-visible {
  border-color: hsl(var(--hue), 92%, 25%);
  outline-color: hsl(var(--hue), 92%, 25%);
}
.btn-primary {
  background-color: hsl(var(--hue), 92%, 25%);
  color: #fff;
}
.btn-primary:hover {
  background-color: hsl(var(--hue), 92%, 35%);
}
.btn-outline-primary {
  background-color: #fff;
  border-color: hsl(var(--hue), 92%, 25%);
  color: hsl(var(--hue), 92%, 25%);
}
.btn-outline-primary:hover {
  background-color: hsl(var(--hue), 80%, 93%);
}
.btn-link {
  text-decoration: underline 1px solid hsl(var(--hue), 92%, 25%);
  color: hsl(var(--hue), 92%, 25%);
}
.btn-lg {
  border-radius: 1em;
  font-size: 1.0625rem;
}

/*****************
    Images
******************/
figure {
  margin: 2em 0;
}
figure img {
  border-radius: 0.325em;
  margin-bottom: 0.25em;
}

figcaption {
  color: #666;
  font-size: 0.75rem;
  font-weight: 500;
  text-align: center;
}

/*****************
    Avatars
******************/
.avatar {
  background-color: var(--grey-500);
  border-radius: 50%;
  color: var(--clr-text-white);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 1.125rem;
  margin-right: 0.5em;
  height: 42px;
  width: 42px;
  user-select: none;
  overflow: hidden;
}

/*****************
    Modal
******************/
.modal {
  display: none;
  position: fixed;
}
.modal::before {
  position: fixed;
  content: "";
  inset: 0;
  background-color: #0a0a0a;
  opacity: 0.7;
}
.modal.show {
  display: block;
}
.modal__container {
  background-color: #fff;
  border-radius: 1em;
  border: 2px solid #666;
  min-width: 300px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 1.5em;
}
.modal h3 {
  margin-bottom: 1em;
}
.modal button {
  display: block;
  margin-left: auto;
}

/*****************
    Typography
******************/
/* Heading Styles */
h1,
h2,
h3,
h4 {
  font-weight: 500;
  margin-bottom: 0.3em;
  line-height: 1;
}

:is(h1, h2, h3) span {
  color: hsl(var(--hue), 92%, 25%);
}

h1,
h2 {
  font-family: "Oswald", serif;
}

h1 {
  font-size: clamp(2rem, 4vw, 2.75rem);
}

h2 {
  font-size: clamp(1.75rem, 3vw, 2rem);
}

h3 {
  font-size: clamp(1.25rem, 2vw, 1.5rem);
}

/* Link Styles */
a {
  font-size: inherit;
  color: #0066dd;
  text-decoration: underline 2px transparent;
  transition: text-decoration-color 200ms ease-in-out;
}
a:focus, a:focus-visible {
  border-color: hsl(var(--hue), 92%, 25%);
  outline-color: hsl(var(--hue), 92%, 25%);
}
a:hover {
  text-decoration-color: #0066dd;
}

/* Paragraph Styles */
p {
  color: #45454f;
  font-size: 1rem;
  line-height: 1.4;
  margin-bottom: 1em;
}

/* Extra font classes */
.fs-small {
  font-size: 0.75rem;
}

/*****************
    Top bar Section
******************/
.topbar {
  background-color: #e2e2e7;
  text-align: center;
  padding: 10px;
}

/*****************
    Navbar Section
******************/
.navbar {
  border-bottom: 1px solid #e2e2e7;
  padding-top: 12px;
  padding-bottom: 12px;
}
.navbar-container {
  flex-wrap: wrap;
}
.navbar .logo {
  letter-spacing: 0.25px;
  text-transform: uppercase;
  font-weight: 500;
}
.navbar .logo span {
  color: #402BCA;
}
.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.75em;
}
.navbar-list, .navbar-login-list {
  list-style: none;
  padding: 0;
  display: flex;
  align-items: center;
  gap: 1em;
}
.navbar-list {
  margin-left: auto;
}
.navbar .nav-link {
  color: #0a0a0a;
  font-weight: 500;
  font-size: 1.0625rem;
}

/*****************
    Footer Section
******************/
.footer {
  background-color: #f5f5f7;
  padding-top: 1em;
  padding-bottom: 1em;
  margin-top: auto;
}

/*****************
    Form Layout
******************/
.form {
  max-width: 400px;
  margin: 0 auto;
}
.form__heading {
  text-align: center;
  margin-bottom: 1em;
}
.form__control {
  display: flex;
  flex-direction: column;
  gap: 0.125em;
  margin-bottom: 2em;
}
.form label {
  color: #45454f;
  cursor: pointer;
  font-size: 1.0625rem;
}
.form input[type=text],
.form input[type=tel],
.form input[type=email],
.form textarea {
  border-radius: 0.325em;
}
.form .input-error {
  color: red;
  font-size: 0.75rem;
  margin-bottom: 0;
  text-align: end;
}
.form__buttons {
  display: flex;
  column-gap: 1em;
  justify-content: end;
}

/*****************
    Sidebar Section
******************/
.sidebar {
  background-color: #fff;
  border-left: 1px solid #e2e2e7;
  border-right: 1px solid #e2e2e7;
  flex: 1 0 300px;
  min-height: 100vh;
  position: relative;
  display: none;
  transform: translateX(100%);
  transition: transform 300ms ease;
}
@media (min-width: 1000px) {
  .sidebar {
    display: none;
    transform: translateX(100%);
  }
  .sidebar.show {
    display: block;
    transform: translateX(0);
  }
}
.sidebar-container {
  display: inline-block;
  position: sticky;
  top: 0;
  padding: 1em;
  width: 100%;
}
.sidebar__header {
  display: grid;
  gap: 1em;
}
.sidebar .btn--black {
  border-radius: 1.5em;
  justify-content: center;
  width: 100%;
}
.sidebar .btn--black:hover {
  background-color: hsl(var(--hue), 92%, 25%);
}
.sidebar input {
  border-radius: 1.5em;
  font-size: 0.875rem;
}
.sidebar__all-skills {
  margin-top: 1em;
}
.sidebar__all-skills h2 {
  color: #0a0a0a;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
  font-size: clamp(1.0625rem, 2vw, 1.25rem);
  letter-spacing: -0.5px;
  margin-top: 2em;
  margin-bottom: 0.75em;
}
.sidebar__skills {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5em;
}
.sidebar__skills span {
  cursor: default;
  background-color: #f5f5f7;
  border: 2px dashed transparent;
  border-radius: 1.5em;
  color: #45454f;
  font-size: 0.875rem;
  padding: 0.25em 0.75em;
}
.sidebar__skills span:hover {
  border-color: hsl(var(--hue), 92%, 25%);
}

/*****************
    Hero Section
******************/
.hero {
  display: flex;
  justify-content: space-between;
  align-items: start;
  gap: 2em;
  padding: 1em 0;
}
@media (max-width: 578px) {
  .hero {
    flex-direction: column-reverse;
  }
}
.hero-content {
  align-self: center;
  flex-grow: 1;
}
.hero-content small {
  display: block;
  margin-bottom: 1rem;
  text-transform: uppercase;
}
.hero-content h1 {
  margin-bottom: 0.125em;
}
.hero-content p:first-of-type {
  font-weight: 500;
  margin-bottom: 0.5em;
}
.hero-content em {
  font-family: serif;
  font-weight: 400;
}
.hero-image {
  align-self: stretch;
  flex: 1 1 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  background-image: radial-gradient(#666 1px, #ffffff 1px);
  background-size: 18px 18px;
  height: 330px;
}
.hero-image img {
  opacity: 1;
}

/*****************
    Featured Product Section
******************/
.homepage-featured-product {
  border: 1px solid transparent;
}
.homepage-featured-product:hover {
  border-color: hsl(var(--hue), 92%, 25%);
}
.homepage-product__image {
  display: block;
}
.homepage-product__image > img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
}

/* Blog Page Styles */
/*****************
    Blog Section
******************/
.blog {
  margin: 2em 0 4em;
}
.blog__post-list {
  list-style: none;
  padding: 0;
}
.blog__post-item {
  display: flex;
  border-bottom: 2px solid #e2e2e7;
  gap: 0.5em;
  padding: 1.5em 0;
}
@media (min-width: 600px) {
  .blog__post-item {
    padding-left: 2em;
    padding-right: 2em;
  }
}
.blog__post-item__content {
  display: grid;
  flex: 0 1 76%;
}
.blog__post-item__content h3 {
  font-size: clamp(1.25rem, 2vw, 1.5rem);
  margin-bottom: 0;
  margin-top: 0.25em;
  letter-spacing: 0;
  line-height: 1.2;
}
.blog__post-item__content h3 a {
  color: #0a0a0a;
}
.blog__post-item__content h3 a:hover {
  text-decoration-color: transparent;
  color: hsl(var(--hue), 92%, 25%);
}
.blog__post-item__content p {
  display: none;
}
@media (min-width: 600px) {
  .blog__post-item__content p {
    display: block;
  }
}
.blog__post-item__date {
  color: #666;
}
.blog__post-item__meta {
  align-self: baseline;
  margin-top: 0.5em;
}
.blog__post-item__meta span {
  color: #45454f;
  font-size: 0.75rem;
}
.blog__post-item__meta span:first-of-type {
  background-color: #f5f5f7;
  border-radius: 1.5em;
  font-weight: 500;
  letter-spacing: -0.25px;
  margin-right: 0.5em;
  padding: 0.5em 1em;
}
.blog__post-item__image {
  border-radius: 0.325em;
  overflow: hidden;
  width: 120px;
  height: 120px;
}
.blog__post-item__image img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}
@media (min-width: 600px) {
  .blog__post-item__image {
    width: 220px;
    height: 180px;
  }
}
.blog__tags {
  border-bottom: 1px solid #f2f2f4;
  border-radius: 0.325em;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 0.125em;
  background-color: #f5f5f7;
  padding: 1.25em 1.5em 0.875em;
}
.blog__tags > span {
  text-transform: uppercase;
  letter-spacing: 2px;
}
.blog__tags-list {
  list-style: none;
  display: flex;
  align-items: center;
  column-gap: 0.5em;
  padding: 0;
  min-height: 54px;
  overflow-x: auto;
  overflow-y: hidden;
}
.blog__tags-list li:first-of-type a {
  display: flex;
  align-items: center;
  gap: 0.125em;
}
.blog__tags-list li:first-of-type a span {
  color: hsl(var(--hue), 92%, 25%);
  font-size: 1.0625rem;
}
.blog__tags-item-link {
  background-color: #fff;
  border: 2px solid #e2e2e7;
  border-radius: 0.325em;
  color: #45454f;
  font-size: 0.875rem;
  font-weight: 500;
  padding: 0.5em 0.75em;
  overflow: hidden;
  white-space: nowrap;
}
.blog__tags-item-link.current {
  background-color: hsl(var(--hue), 80%, 98%);
  border-color: hsl(var(--hue), 92%, 25%);
}
.blog__tags-item-link:hover {
  border-color: hsl(var(--hue), 92%, 25%);
  text-decoration-color: transparent;
}

/* Error Page Styles */
/*****************
    Error page
******************/
.error-page {
  background-color: #f5f5f7;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh;
}
.error-page-container {
  max-width: 600px;
}
.error-page h2 {
  margin-bottom: 0.5em;
}
.error-page p {
  color: #45454f;
  margin-bottom: 1em;
}